@import '../common-imports';

.skillet-grid-d {
    padding-bottom: 2rem;

    @include media-breakpoint-up(md) {
        padding-bottom: 3rem;
    }

    @include media-breakpoint-up(lg) {
        padding-bottom: 7.6rem;
    }

    &-container {
        margin-bottom: -3.2rem;

        .card-b06 {
            margin-bottom: 3.2rem;

            &-figure {
                height: 19.2rem;

                @include media-breakpoint-up(md) {
                    flex: 0 0 23.45%;
                    height: 12.4rem;
                }

                @include media-breakpoint-up(lg) {
                    height: 15rem;
                    flex: 0 0 21%;
                }
            }

            &-content {
                .subheadline-2-semibold {
                    @media(min-width: 768px) and (max-width: 1024px) {
                        font-size: 2.6rem;
                        line-height: 3.6rem;
                    }
                }
            }
        }
    }

    &--hasNoSvg {
        .hasSvg {
            display: none;
        }
    }

    &__hasBg {
        position: relative;

        padding-bottom: calc(734 / 1442 * 100%);

        &-footer {
            padding-bottom: calc(251 / 1442 * 100%);
            margin-top: 10rem;

            @include media-breakpoint-up(md) {
                margin-top: 12rem;
            }

            @include media-breakpoint-up(lg) {
                margin-top: 5.2rem;
            }
        }
    }

    &--hasSvg {
        display: block;
        background-color: transparent !important;

        .skillet-grid-d__hasBg {
            svg {
                width: 101%;
                height: 102%;
                @include position(absolute);
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
            }
        }

        .skillet-grid-d {
            &-wrapper {
                position: relative;

                &:before {
                    content: '';
                    z-index: 0;
                    position: absolute;
                    left: 0;
                    background-color: $dark-green;
                    width: 100%;
                    pointer-events: none;
                    height: 100%;
                    top: 1%;

                    @include media-breakpoint-up(md) {
                        top: 3%;
                    }

                    @include media-breakpoint-up(lg) {
                        top: 9%;
                    }
                }

                .container {
                    position: relative;
                    z-index: 1;
                    margin-top: -5%;

                    @include media-breakpoint-up(lg) {
                        margin-top: -9.4%;
                    }
                }
            }
        }

        &.bg-light {
            background-color: $dark-green !important;

            .skillet-grid-d {
                &__hasBg {
                    .quote-svg-bg {
                        fill: $light;
                    }
                }

                &-wrapper {
                    color: $secondary;

                    &::before {
                        background-color: $light;
                    }
                }

                &-content {

                    .text-white {
                        color: $secondary !important;
                    }
                }
            }
        }

        &.bg-sand {
            background-color: $dark-green !important;

            .skillet-grid-d {
                &__hasBg {
                    .quote-svg-bg {
                        fill: $sand;
                    }
                }

                &-wrapper {
                    color: $secondary;

                    &::before {
                        background-color: $sand;
                    }
                }

                &-content {

                    .text-white {
                        color: $secondary !important;
                    }
                }
            }
        }

        &.bg-dark-green {
            background-color: $sand !important;

            .skillet-grid-d {
                &__hasBg {
                    .quote-svg-bg {
                        fill: $dark-green;
                    }
                }

                &-wrapper {
                    color: $light;

                    &::before {
                        background-color: $dark-green;
                    }
                }
            }
        }
    }

